<template>
  <div class="recording">
    <v-card>
      <div>
        <v-btn color="success" @click="display_update">Display</v-btn>
        <v-btn color="success" @click="delete_all">Clear</v-btn>
        <v-switch v-model="recording" color="red" label="Recording"></v-switch>
      </div>
      <div v-if="this.head !== null">
        <v-data-table :headers="this.keys" :items="this.head" class="elevation-1">
          <template v-slot:items="props">
            <td>{{props.item.id}}</td>
            <td class="text-xs-right">{{props.item.date}}</td>
            <td class="text-xs-right">{{props.item.accX}}</td>
            <td class="text-xs-right">{{props.item.accY}}</td>
            <td class="text-xs-right">{{props.item.accZ}}</td>
          </template>
        </v-data-table>
      </div>
    </v-card>
    <v-card class="mx-auto" color="grey lighten-4" max-width="600">
      <v-card-text
        class="pt-4"
        style="position: relative;"
      >
        <div class="font-weight-light grey--text title mb-2">Recording Sensors</div>
        <h3 class="display-1 font-weight-light orange--text mb-2">Acceleration</h3>
        <v-sparkline :value="acc_x" color="primary"></v-sparkline>
        <div class="font-weight-light title mb-2">
          X: {{acc_x.slice(-1)[0]}}
        </div>
        <v-sparkline :value="acc_y" color="green"></v-sparkline>
        <div class="font-weight-light title mb-2">
          Y: {{acc_y.slice(-1)[0]}}
        </div>
        <v-sparkline :value="acc_z" color="red"></v-sparkline>
        <div class="font-weight-light title mb-2">
          Z: {{acc_z.slice(-1)[0]}}
        </div>
        <h3 class="display-1 font-weight-light orange--text mb-2">Gyroscope</h3>
        <div class="font-weight-light title mb-2">
          beta: {{gyro_beta}}
        </div>
        <div class="font-weight-light title mb-2">
          gamma: {{gyro_gamma}}
        </div>
        <div class="font-weight-light title mb-2">
          alpha: {{gyro_alpha}}
        </div>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
export default{
  data () {
    return {
      recording: false,
      number: 1,
      accelerometer: null,
      acc_x: [0.0],
      acc_y: [0.0],
      acc_z: [0.0],
      gyroscope: null,
      gyro_beta: 0.0,
      gyro_gamma: 0.0,
      gyro_alpha: 0.0,
      head: null,
      keys: [
        { text: 'ID', value: "id" },
        { text: 'Date', value: "date"},
        { text: '加速度X', value: 'accX', sortable: false},
        { text: '加速度Y', value: 'accY', sortable: false},
        { text: '加速度Z', value: 'accZ', sortable: false}
      ]

    }
  },
  methods: {
    devicemotion () {
      let accX = this.accelerometer.x
      let accY = this.accelerometer.y
      let accZ = this.accelerometer.z
      this.acc_x.push(accX)
      if(this.acc_x.length > 1000){
        this.acc_x.shift()
      }
      this.acc_y.push(accY)
      if(this.acc_y.length > 1000){
        this.acc_y.shift()
      }
      this.acc_z.push(accZ)
      if(this.acc_z.length > 1000){
        this.acc_z.shift()
      }
      if(this.recording === true){
        this.add(accX, accY, accZ)
      }
    },
    deviceorientation () {
      this.gyro_beta = this.gyroscope.x
      this.gyro_gamma = this.gyroscope.y
      this.gyro_alpha = this.gyroscope.z
    },
    add (accX, accY, accZ) {
      this.$db.sensors.put({
        accX: accX,
        accY: accY,
        accZ: accZ,
        number: this.number,
        date: new Date()
      }).then((id) => {
        console.log('record saved: ', id)
      })
    },
    display_update () {
      this.$db.sensors.limit(20).toArray().then((sensors) => {
        this.head = sensors
      })
    },
    delete_all () {
      this.$db.sensors.clear()
    },
    export_db () {
      this.$db.export()
    }
  },
  created () {
    // eslint-disable-next-line
    this.accelerometer = new Accelerometer({frequency: 50})
    // eslint-disable-next-line
    this.accelerometer.addEventListener('activate', (event) => {
      console.log("加速度センサが有効になりました")
    })
    this.accelerometer.addEventListener('reading', this.devicemotion)
    // eslint-disable-next-line
    this.accelerometer.addEventListener('error', (event) => {
      console.log("加速度センサでエラーが発生しました")
      console.log(event.error)
      this.accelerometer.stop()
    })
    this.accelerometer.start()
    
    // eslint-disable-next-line
    this.gyroscope = new Gyroscope({frequency: 50})
    // eslint-disable-next-line
    this.gyroscope.addEventListener('activate', (event) => {
      console.log("ジャイロスコープが有効になりました")
    })
    this.gyroscope.addEventListener('reading', this.deviceorientation)
    // eslint-disable-next-line
    this.gyroscope.addEventListener('error', (event) => {
      console.log("ジャイロスコープでエラーが発生しました")
      console.log(event.error)
      this.gyroscope.stop()
    })
    this.gyroscope.start()
  },
  destroyed () {
    this.accelerometer.stop()
    this.gyroscope.stop()
  }
}
</script>